.container {
    margin:0px;
    padding: 25px 0 25px 25px;
    border-bottom: 1px solid #9fb1b8;
}
.image{
    border-radius: 15px;
    margin-top: 25px;
    width:200px;
    margin-left:220px;
    border: 2px dashed #000f67;
}
.name{
    color:#000b72;
    transition: ease color 0.3s;
}
.name:hover{
    color:#000B34;
}

h1{
    margin: 0px;
    font-size: 40px;
}
p{
    margin:0px;
    width:700px;
}
.text{
    margin-top:10px;
    font-size: 18px;
}
